import React from "react";
import { useParams } from "react-router-dom";

const Product = ({ data }) => {
  const { productId } = useParams();
  const product = data.find((p) => p.id === Number(productId));
  let productData;

  if (product) {
    productData = (
        <div  >
        <img src={product.imageUrl} className="d-100 w-100"  alt="bottled products"/>
          <div className="text-left row">
          <h3>{product.name}</h3>
        <p>{product.description}</p>
        <p>{product.usage}</p>

        <p>{product.ingredients}</p>

        <h5>{product.status}</h5>
          </div>
        </div>
    );
  } else {
    productData = <h2>Perdon. Ese producto no existe. </h2>;
  }
  return (

 <div className="bg-white text-center">
        <div  style={{ display: "flex" }}>
      <div
        style={{
          width: "80%",
          height: "40%",
          margin: "auto",
          background: "#ffffff",
          padding: "10px"
        }}
      >
        {productData}
      </div>
    </div>
    </div>
  );
};

export default Product;
